import React, { useState } from "react"
import PropTypes from "prop-types"
import Button from "../components/button"

import {
  Box,
  Flex,
  Label,
  Input,
  Select,
  Textarea,
  Radio,
  Checkbox,
  Slider,
  Text,
} from "theme-ui"

const voivodeships = [
  "dolnolskie",
  "kujawsko-omorskie",
  "lubelskie",
  "lubuskie",
  "dzkie",
  "maopolskie",
  "mazowieckie",
  "opolskie",
  "podkarpackie",
  "podlaskie",
  "pomorskie",
  "lskie",
  "witokrzyskie",
  "warmisko-mazurskie",
  "wielkopolskie",
  "zachodniopomorskie",
]

const Form = () => {
  const [form, setForm] = useState({})
  const handlePrint = () => {
    window.localStorage.setItem("form", JSON.stringify(form))
    window.open("/pdf", "_blank")
  }
  const handleSetForm = (key, value) => setForm({ ...form, [key]: value })

  return (
    <div
      style={{
        display: "flex",
        alignItems: "center",
        flexDirection: "column",
        padding: "2rem 1rem",
      }}
    >

      <Text>
      Aby dostac pdf, wypelnij ponizszy formularz
      </Text>  
      <Box as="form" onSubmit={e => e.preventDefault()}>
        <Label htmlFor="name">Imie</Label>
        <Input
          name="name"
          id="name"
          mb={3}
          onChange={({ target: { value } }) => handleSetForm("name", value)}
        />
        <Label htmlFor="surname">Nazwisko</Label>
        <Input
          name="surname"
          id="surname"
          mb={3}
          onChange={({ target: { value } }) => handleSetForm("surname", value)}
        />
        <Label htmlFor="sound">Wojewodztwo</Label>
        <Select name="sound" id="sound" mb={3}>
          {voivodeships.map(v => (
            <option>{v}</option>
          ))}
        </Select>
        <Label htmlFor="county">Powiat</Label>
        <Select name="county" id="county" mb={3}>
          {voivodeships.map(v => (
            <option>{v}</option>
          ))}
        </Select>
        <Label htmlFor="comment">Comment</Label>
        <Textarea name="comment" id="comment" rows="6" mb={3} />
        <Button onClick={handlePrint} variant="secondary">
          Drukuj
        </Button>
      </Box>
    </div>
  )
}

export default Form
